<template>
  <section class="search-food">
    <h1>Calc daily nutritions</h1>
    <label for="searchFood" class="default-input">
      <div class="label-wrap"><i class="material-icons">search</i>Search</div>
        <input
          type="text"
          id="searchFood"
          name="searchFood"
          class="default-input__input center"
          :class="{ inputEmpty: isSearchEmpty }"
          v-model='search'
          @input="wrapHandleInput()"
          @keyup="detectInputChange()"
          @keyup.delete="isLoading = false"
          placeholder="Write a product name"
        />
    </label>
    <div v-if="isSearchEmpty" class="error-empty-field">Search for food!</div>
    <div v-if="isLoading" class="spinner"></div>
  </section>
</template>

<style lang="stylus">
.search-food__info
  &-icon
    position absolute
    left calc(var(--edge-space) + 10px)
    font-size 25px
    cursor pointer
    color var(--secondary)
    transition color 0.2s ease
    font-size 30px
    &:hover
      color var(--tertiary)
.label-wrap
  display flex
  align-items center
  justify-content center

.spinner
  width 50px
  height 50px
  margin-top 40px
  border 4px #d8d8d8 solid
  border-top 4px var(--primary) solid
  border-radius 50%
  animation animateSpinner .6s infinite linear

@keyframes animateSpinner {
  from { transform: rotate(0deg); }
	to { transform: rotate(359deg); }
}
</style>

<script>
import axios from 'axios';

import { EventBus } from './event-bus';
import { debounce } from 'lodash';

export default {
  name: 'search',
  props: ['value'],
  data() {
    return {
      search: '',
      isLoading: false,
      isSearchEmpty: false,
    };
  },
  mounted() {
    EventBus.$on('searchEmpty', () => {
      this.isSearchEmpty = true;
    });
  },
  methods: {
    wrapHandleInput() {
      this.isLoading = true;
      this.handleInput();
    },
    handleInput: debounce(function () {
      axios({
        method: 'get',
        url: `https://trackapi.nutritionix.com/v2/search/instant?query=${this.search}`,
        headers: {
          'x-app-id': 'c4bc3eb9',
          'x-app-key': '0f2635663c4ca9bfd681157224d9106d',
          'x-remote-user-id': '0',
        },
      }).then((response) => {
        const results = (response.data.common);
        this.$emit('getList', results);
        this.isLoading = false;
      }).catch((error) => {
        console.log(error);
      });
    },
    500),
    detectInputChange() {
      if (this.search === '') {
        this.$emit('emptyInput');
      }
      this.isSearchEmpty = false;
    },
  },
};
</script>
