export function translateIds (el) {
  switch (el) {
    case 203:
      return 'Protein';
    case 204:
      return 'Fat';
    case 255:
      return 'Water';
    case 269:
      return 'Carbs';
    case 301:
      return 'Calcium';
    case 303:
      return 'Iron';
    case 304:
      return 'Magnesium';
    case 306:
      return 'Potassium';
    case 307:
      return 'Sodium';
    case 309:
      return 'Zinc';
    case 320:
      return 'Vit. A';
    case 324:
      return 'Vit. D';
    case 401:
      return 'Vit. C';
    case 415:
      return 'Vit. B6';
    case 418:
      return 'Vit. B12';
    case 430:
      return 'Vit. K';
    case 573:
      return 'Vit. E';
  }
}
